import React, { Component, Fragment } from "react";
import { userData, data } from "./Data";
import { dateGetter, dateIndex } from "./MainPage";
import ItemCard from "./ItemCard";
import { S, wait } from "./alabsi";

let mealValueTmp = "";
export default class RecentlyAdded extends Component {
  state = {
    meal:
      mealValueTmp.length > 0
        ? mealValueTmp
        : window.location.hash.split("#")[1]
        ? window.location.hash.split("#")[1]
        : "breakfast",
  };

  componentDidMount() {
    if (this.props.doAnimation) {
      this.introPageTransition();
    } else {
      this.introPageFadeTransition();
    }
    window.addEventListener("popstate", () => {
      if (window.location.pathname === "/" || window.location.pathname.includes("search")) mealValueTmp = "";
    });
  }

  introPageFadeTransition = async () => {
    const page = document.getElementById("recentPage");
    page.style.opacity = "0";
    page.style.transition = "opacity 350ms";
    await wait(20);
    page.style.opacity = "1";
    await wait(350);
    page.removeAttribute("style");
  };

  introPageTransition = async () => {
    const page = document.getElementById("recentPage");
    const pageHeader = document.getElementById("recentHeader");

    page.style.cssText = `position: fixed; bottom: 50%; left: 50%; opacity: 0; transform: translate(-50%, 100%); transition: bottom 200ms`;
    pageHeader.style.position = "relative";

    await wait(10);
    page.style.bottom = "100%";
    page.style.opacity = "1";

    await wait(200);
    page.removeAttribute("style");
    pageHeader.removeAttribute("style");
  };

  getRecentMeal = (mealName, dateIndex) => {
    const getDataByDate = userData.diary.filter((e) => e.date === dateGetter(dateIndex))[0];
    if (getDataByDate && getDataByDate[mealName]) {
      const getDataById = getDataByDate[mealName].map(
        (e) => (e = Object.assign({}, data.filter((el) => el.id === e[0])[0], { value: e[1], input: e[2] }))
      );
      return getDataById.map((e) => (
        <ItemCard
          key={e.id + Math.random() * 1000}
          name={e.name}
          kcal={Math.round((e.kcal / 100) * (e.value * e.input))}
          serving={
            e.value === 1 ? e.input + " غرام " : e.input + "  " + e.serving.filter((el) => el[1] === e.value)[0][0]
          }
          id={e.id}
          brand={e.brand}
          description={e.description}
          value={e.value}
          input={e.input}
        />
      ));
    }
  };

  addAllButtonHandle = (toAddMeal, mealName, i) => {
    const getDataByDate = userData.diary.filter((e) => e.date === dateGetter(i))[0];
    let itemsArray;
    if (getDataByDate && getDataByDate[mealName]) {
      itemsArray = [...getDataByDate[mealName]];
    }

    const addByDate = userData.diary.filter((e) => e.date === dateGetter(dateIndex))[0];
    if (window.location.pathname.includes("CreateNewMeal") || window.location.pathname.includes("EditMeal")) {
	  userData.favorite.tmpMeal.mealContent.push(...itemsArray);
	  this.showToast("تمت الإضافة على الوجبة");
    } else if (addByDate) {
	  addByDate[toAddMeal] ? addByDate[toAddMeal].push(...itemsArray) : (addByDate[toAddMeal] = itemsArray);
	  this.showToast("تمت الإضافة على " + this.checkHash(toAddMeal));
    } else {
		this.showToast("تمت الإضافة على " + this.checkHash(toAddMeal));
      userData.diary.push({ date: dateGetter(dateIndex), [toAddMeal]: itemsArray });
    }
  };

  renderRecent = () => {
    let content = [];
    for (let i = 0; i > -8; i--) {
      if (this.getRecentMeal(this.state.meal, i)) {
        content.push([
          <Fragment key={Math.random() * 1000}>
            <p id="recentDateText">{dateGetter(i)}</p>
            <p className="recentAddAll" onClick={() => this.addAllButtonHandle(window.location.hash.split("#")[1], this.state.meal, i)}>
              إضافة الكل ↓
            </p>
            <div>{this.getRecentMeal(this.state.meal, i)}</div>
          </Fragment>,
        ]);
      }
    }
    return content;
  };

  selectMealHandle = (e) => {
    this.setState({ meal: e.target.value });
    mealValueTmp = e.target.value;
  };

  backButtonHandle = () => window.history.back();

  checkHash = (h) => {
    let hash;
    switch (h) {
      case "breakfast":
        hash = "الفطور";
        break;
      case "lunch":
        hash = "الغداء";
        break;
      case "dinner":
        hash = "العشاء";
        break;
      case "snacks":
        hash = "وجبة خفيفة";
        break;
      default:
        break;
    }
    return hash;
  };

  showToast = async (text) => {
    S("#recentToast").text(text).css("display", "block");
    await wait(20);
    S("#recentToast").css("opacity", 1).css("bottom", "75px");
    await wait(2000);
    S("#recentToast").css("opacity", 0).css("bottom", "0px");
    await wait(350);
    S("#recentToast").removeAttr("style");
  };

  render() {
    return (
      <div id="recentPage">
		  <p id="recentToast"></p>
        <header id="recentHeader">
          <p id="recentHeaderTitle">المضافة مؤخراً</p>
          <svg id="recentBack" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" onClick={this.backButtonHandle}>
            <path d="M20 11H7.83l5.59-5.59L12 4l-8 8 8 8 1.41-1.41L7.83 13H20v-2z" />
          </svg>
        </header>
        <select id="recentSelectMeal" value={this.state.meal} onChange={this.selectMealHandle}>
          <option className="itemViewerSelectMealItems" value="breakfast">
            الفطور
          </option>
          <option className="itemViewerSelectMealItems" value="lunch">
            الغداء
          </option>
          <option className="itemViewerSelectMealItems" value="dinner">
            العشاء
          </option>
          <option className="itemViewerSelectMealItems" value="snacks">
            وجبة خفيفة
          </option>
        </select>
        <p id="recentNoResults" style={{ display: this.renderRecent().length > 0 ? "none" : "block" }}>
          لا يوجد عناصر مضافة في آخر اسبوع على {this.checkHash(this.state.meal)}
        </p>
        <div>{this.renderRecent()}</div>
      </div>
    );
  }
}
